import ProtectedRoute from "@/components/auth/ProtectedRoute";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { getBooksByUser, getReviewsByUser, getUserById, getBookById } from "@/lib/data";
import { Book, Edit } from "lucide-react";
import Link from "next/link";
import Rating from "@/components/ui/Rating";

// This component needs to be client-side to access auth context to get the user ID
// But data fetching should be on server. This is a classic dilemma.
// For this app, we'll fetch data for a mock user '1' to show the page structure.
// In a real app, you'd likely fetch this data in a client component after getting the user ID.

export default async function ProfilePage() {
  const userId = "1"; // Mocking current user ID
  const user = await getUserById(userId);
  const userBooks = await getBooksByUser(userId);
  const userReviews = await getReviewsByUser(userId);

  if (!user) {
    return <ProtectedRoute><div>User not found.</div></ProtectedRoute>
  }
  
  return (
    <ProtectedRoute>
      <div className="container mx-auto py-6 sm:py-8 px-4 sm:px-6 lg:px-8">
        <div className="flex flex-col sm:flex-row items-center sm:items-start gap-4 sm:gap-8 mb-8 sm:mb-10">
            <Avatar className="h-20 w-20 sm:h-24 sm:w-24 border-2 border-primary">
                <AvatarImage src={`https://api.dicebear.com/8.x/initials/svg?seed=${user.name}`} />
                <AvatarFallback className="text-3xl">{user.name.charAt(0)}</AvatarFallback>
            </Avatar>
            <div className="text-center sm:text-left">
                <h1 className="text-2xl sm:text-3xl font-bold font-headline">{user.name}</h1>
                <p className="text-muted-foreground">{user.email}</p>
            </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 sm:gap-8">
            <Card>
                <CardHeader>
                    <CardTitle className="flex items-center gap-2 text-xl sm:text-2xl"><Book className="h-5 w-5"/> Books Added</CardTitle>
                </CardHeader>
                <CardContent>
                    {userBooks.length > 0 ? (
                        <ul className="space-y-4">
                            {userBooks.map(book => (
                                <li key={book.id} className="flex justify-between items-center">
                                    <div>
                                        <Link href={`/books/${book.id}`} className="font-semibold hover:underline">{book.title}</Link>
                                        <p className="text-sm text-muted-foreground">{book.author}</p>
                                    </div>
                                    <Link href={`/edit-book/${book.id}`} className="text-sm text-primary hover:underline flex items-center gap-1">
                                        <Edit className="h-3 w-3" /> <span className="hidden sm:inline">Edit</span>
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <p className="text-muted-foreground">You haven&apos;t added any books yet.</p>
                    )}
                </CardContent>
            </Card>

            <Card>
                <CardHeader>
                    <CardTitle className="text-xl sm:text-2xl">Your Reviews</CardTitle>
                </CardHeader>
                <CardContent>
                    {userReviews.length > 0 ? (
                         <ul className="space-y-6">
                            {userReviews.map(async (review) => {
                                const book = await getBookById(review.bookId);
                                return (
                                    <li key={review.id}>
                                        {book && <p className="font-semibold text-sm">Review for <Link href={`/books/${book.id}`} className="text-primary hover:underline">{book.title}</Link></p>}
                                        <Rating rating={review.rating} className="my-1"/>
                                        <p className="text-sm text-muted-foreground italic">&quot;{review.reviewText}&quot;</p>
                                    </li>
                                )
                            })}
                        </ul>
                    ) : (
                        <p className="text-muted-foreground">You haven&apos;t written any reviews yet.</p>
                    )}
                </CardContent>
            </Card>
        </div>
      </div>
    </ProtectedRoute>
  );
}
